<template>
  <div>
    <div
      class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted"
    >
      <div class="mui-scroll">
        <a :class="['mui-control-item', item.id==0?'mui-active':'']"
          v-for="item in list"
          :key="item.id"
          @tap="getphoto(item.id)"
        >{{item.title}}</a>
      </div>
    </div>
<!-- 图片列表 -->
    <ul class="photo-list">
      <router-link v-for="item in plist" :key="item.id" :to="'/home/photoinfo/' + item.id" tag="li">
        <img v-lazy="item.img_url">
        <div class="info">
          <h1 class="info-title">{{ item.title }}</h1>
          <div class="info-body">{{ item.zhaiyao }}</div>
        </div>
      </router-link>
    </ul>

  
  </div>
</template>
<script>
import mui from "../../lib/mui/js/mui.min.js";
// mui('body').on('tap','a',function(){
//           window.top.location.href=this.href;
//    }); 
export default {
  data() {
    return {
      list: [],
      plist: []
    };
  },
  created() {
    this.getphoto();
    this.getphotos(0);
  },
  mounted() {
    mui(".mui-scroll-wrapper").scroll({
      deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
    });
  },
  methods: {
    getphoto() {
      this.$http
        .get("http://www.lovegf.cn:8899/api/getimgcategory")
        .then(result => {
          // console.log(result);
          if (result.body.status === 0) {
            result.body.message.unshift({ title: "全部", id: 0 });
            this.list = result.body.message;
          } else {
            Toast("加载失败");
          }
        });
    },
    getphotos(cateId){
      this.$http.get("http://www.lovegf.cn:8899/api/getimages/" + cateId).then(result=>{
        if(result.body.status===0){
          this.plist=result.body.message
        }else{
          Toast("加载图片失败")
        }
      })
    }
  }
};
</script>
<style lang="less" scoped>
* {
  touch-action: pan-y; //去除滑动时候警告
}
.photo-list{
  list-style:none;
  margin:0;
  padding:10px;
  padding-bottom:0;
  li{
    background-color: #ccc;
    text-align: center;
    margin-bottom:10px;
    box-shadow: 0 0 9px #999;
    position:relative;
    img{
      width:100%;
      vertical-align: middle;
    }
    img[lazy="loading"]{
      width: 40px;
      height: 300px;
      margin:auto;
    }
    .info{
      color:white;
      text-align: left;
      position:absolute;
      bottom:0;
      background-color:rgba(0, 0, 0, 0.4);
      max-height:84px;
      .info-title{
        font-size:14px;
      }
      .info-body{
        font-size:13px;
      }
    }
  }
}
</style>