<script>
export default {
  data() {
    return {
      lis: "0",
      flag: "false"
    };
  },
  created(){
    this.flag=this.$route.path==="/home"?false:true
  },
  methods: {
    hui() {
      this.$router.go(-1); //返回上一级
    }
  },
  watch: {
    "$route.path": function(newVal) {
      if (newVal === "/home") {
        this.flag = false;
      } else {
        this.flag = true;
      }
    }
  }
};
