<template>
  <div class="photoinfo-container">
    <h3>{{ photoinfo.title }}</h3>
    <p class="subtitle">
      <span>发表时间：{{ photoinfo.add_time | dateFormat }}</span>
      <span>点击：{{ photoinfo.click }}次</span>
    </p>

    <hr>

    <!-- 缩略图区域 -->
    <div id="thumbs">
      <vue-preview :slides="list" @close="handleClose"></vue-preview>
    </div>

    <!-- 图片内容区域 -->
    <div class="content" v-html="photoinfo.content"></div>

    <!-- 放置一个现成的 评论子组件 -->
    <cmt-box :id="id"></cmt-box>
  </div>
</template>
<script>
// import VuePreview from 'vue-preview'
import comment from "../subcomponents/comment.vue";
export default {
  data() {
    return {
      id: this.$route.params.id,
      photoinfo: {},
      list: []
    };
  },
  created() {
    this.getPhoto();
    this.getThumds();
  },
  methods: {
    getPhoto() {
      this.$http
        .get("http://www.lovegf.cn:8899/api/getimageInfo/" + this.id)
        .then(result => {
          if (result.body.status === 0) {
            this.photoinfo = result.body.message[0];
          }
        });
    },
    getThumds() {
      this.$http
        .get("http://www.lovegf.cn:8899/api/getthumimages/" + this.id)
        .then(result => {
          if (result.body.status === 0) {
            result.body.message.forEach(item => {
              item.w = 600;
              item.h = 400;
              item.msrc = item.src;
            });
            this.list = result.body.message;
          }
        });
    },
    handleClose() {
      console.log(2345);
    }
  },
  components: {
    "cmt-box": comment
  }
};
</script>
<style lang="less" >
.photoinfo-container {
  padding: 3px;
  h3 {
    color: #26a2ff;
    font-size: 15px;
    text-align: center;
    margin: 15px 0;
  }
  .subtitle {
    display: flex;
    justify-content: space-between;
    font-size: 13px;
  }
  .content {
    font-size: 13px;
    line-height: 30px;
  }
  #thumbs .my-gallery {
    overflow: hidden;
    figure {
      padding: 5px;
      width: 100px;
      float: left;
      margin: 0;
      a {
        width: 100%;
        display: block;
        img {
          width: 100%;
          height: 60px;
          display: block;
        }
      }
    }
  }
}
</style>