<template>
<div class="newsinfo-container">
<h3 class="title">{{newlist.title}}</h3>
<p class="subtitle">
   <span>发表时间:{{newlist.add_time|dateFormat}}</span>
   <span>点击:{{newlist.click}} 次</span> 
</p>
<hr>
<div class="content" v-html="newlist.content"></div>
<comment-box :id="this.id"></comment-box>

</div>


</template>
<script>
import comment from '../subcomponents/comment.vue'

export default {
   data(){
       return{
           id:this.$route.params.id ,//接收router-link传的参数
           newlist:[]
       }
   } ,
   created(){
       this.getNewsInfo()
   },
   methods:{
       getNewsInfo(){
           this.$http.get('http://www.lovegf.cn:8899/api/getnew/'+this.id).then(result=>{
                // console.log(result.body)
               if(result.body.status===0){
                   this.newlist=result.body.message[0]
               }else{
                   Toast('获取文章失败')
               }
           })
       }
   },
   components:{
       "comment-box":comment
   }
}
</script>
<style lang="less">
    .newsinfo-container{
        padding:0 4px;
        .title{
            font-size:16px;
            text-align: center;
            margin:15px 0;
            color:red
        }
        .subtitle{
            font-size:13px;
            color:#ff9f22;
            display:flex;
            justify-content:space-between;
        }
        .content{
            img{
                width:100%;
            }
        }
    }
</style>

